.descriptionBlock {
    padding: 20px;
    color: dimgray;
}

.mainPhoto {
    width: 200px;
    border-radius: 100px;
    float: right;
}

.editPhoto {
    float: right;
}

.itemLabel {
    display: inline-block;
    width: 175px;
    vertical-align: top;
}

.contact {
    display: inline-block;
    padding-left: 10px;
    width: 100px;
    vertical-align: top;
}

.status {
    display: inline-block;
    vertical-align: top;
}

.statusBody{
    display: inline-block;
    width: 500px;
    word-break: break-word;
}

.input {
    display: inline-block;
}

.input input {
    width: 200px;
}

.textarea {
    display: inline-block;
}

.textarea textarea {
    width: 300px;
}